import { React, Component } from 'react'
import { Button } from 'reactstrap'
import List from './List'

export default class Lists extends Component{

    renderLists = () => {

        return this.props.lists.map((item) => {
            return (
                <List list={item} darkMode = {this.props.darkMode}></List>
            )
        })
    }

    render() {
        //console.log('rendering Lists')

        const lists = this.renderLists()
        const header = this.props.lists.length === 0 ? 'Списков пока нет' : 'Списки покупок'
        return (
            <>
                <header><h1>{header}</h1></header>
                {lists}
                <Button color="primary" style={{width: 'calc(100% - 20px)', margin: '10px'}} onClick={this.props.togglePapa}>Создать новый список</Button>
            </>
        )
    }

}

