import { React, Component } from 'react'
import { Button } from 'reactstrap'
import broccoli from './broccoli.svg'
import apple from './apple.svg'
import spinach from './spinach.png'
import berry from './berry.svg'

import NewListGroup from './NewListGroup'

export default class NewList extends Component{

    state = {
        name: '',
        data: []
    }

    getData = (art, name, quantity, checked, key) => {

        const arr = [...this.state.data].filter((item)=> {
          return item.key !== key
        })
    
        if (checked) {
          arr.push({art: art, name: name, quantity: quantity, checked: checked, key: key})
        }
    
        this.setState({data: arr})
    }
    
    setListName = (event) => {
        this.setState({
          name: event.target.value
        })
      }

    addNewList = () => {
        this.props.getList(this.state.name, this.state.data)
    }

    arr = [
        {
            name: 'vegetables',
            artName: 'Овощи',
            icon: broccoli,
            data: [
                {name: 'Помидор'}, 
                {name: 'Огурец'},
                {name: 'Перец'}, 
                {name: 'Баклажан'}, 
                {name: 'Кабачок'}, 
                {name: 'Картофель'}, 
                {name: 'Лук'}, 
                {name: 'Морковь'}, 
                {name: 'Авокадо'}, 
                {name: 'Батат'}, 
                {name: 'Свекла'}, 
                {name: 'Чеснок'}, 
            ]
        },

        {
            name: 'fruits',
            artName: 'Фрукты',
            icon: apple,
            data: [
                {name: 'Абрикос'}, 
                {name: 'Ананас'},
                {name: 'Апельсин'},
                {name: 'Арбуз'},
                {name: 'Банан'},
                {name: 'Виноград'},
                {name: 'Гранат'},
                {name: 'Грейпфрут'},
                {name: 'Груша'},
                {name: 'Дыня'},
                {name: 'Киви'},
                {name: 'Лайм'},
                {name: 'Лимон'},
                {name: 'Манго'},
                {name: 'Мандарин'},
                {name: 'Нектарин'},
                {name: 'Персик'},
                {name: 'Слива'},
                {name: 'Яблоко'}
            ]
        },

        {
            name: 'greens',
            artName: 'Зелень',
            icon: spinach,
            data: [
                {name: 'Базилик'}, 
                {name: 'Лук'},
                {name: 'Лук порей'}, 
                {name: 'Петрушка'},
                {name: 'Рукола'},
                {name: 'Салат'}, 
                {name: 'Салат китайский'},
                {name: 'Романо'},
                {name: 'Шпинат'},
            ]
        },

        {
            name: 'berries',
            artName: 'Ягоды',
            icon: berry,
            data: [
                {name: 'Вишня'}, 
                {name: 'Ежевика'},
                {name: 'Земляника'},
                {name: 'Клубника'},
                {name: 'Клюква'}, 
                {name: 'Крыжовник'},
                {name: 'Малина'},
                {name: 'Смородина'},
                {name: 'Хурма'}, 
                {name: 'Черешня'},
                {name: 'Черная смородина'}
            ]
        }
    ]

    renderListGroups = () => {
        return this.arr.map(item => {
            return (
                <NewListGroup 
                getData = {this.getData}
                art = {item.name}
                items = {item.data} 
                artName = {item.artName}
                icon = {item.icon}
                darkMode = {this.props.darkMode}/>
            )
        })
    }

    render() {
        //console.log('rendering New List')

        const listGroups = this.renderListGroups()
        const color = this.props.darkMode === true ? 'white' : 'black'

        return (
            <>
                <header>
                    <input 
                        placeholder="Новый список" 
                        autoFocus
                        value="Новый список "
                        className="newListName"
                        onChange = {this.setListName}
                        style={{color: color}}>
                    </input>
                </header>

                {listGroups}

                <Button 
                    color="primary"
                    onClick={this.addNewList}
                    style={
                        {float: 'right', 
                        margin: '10px'}
                    }>Ok
                </Button>
            </>
        )
    }
}

