import { React, Component } from 'react'
import { Collapse, Button, ListGroup, ListGroupItem } from 'reactstrap'
import NewListItem from './NewListItem'

export default class NewListGroup extends Component{

    state = {
        isOpen: false
    }


    setIsOpen = () => {
        this.setState({isOpen: !this.state.isOpen})
    }

    renderItems = () => {

        return this.props.items.map((item) => {
            return (
                <NewListItem 
                    item={item} 
                    art={this.props.art} 
                    getData={this.props.getData} 
                    published={this.props.published}
                    darkMode={this.props.darkMode}>
                </NewListItem>
            )
        })
    }

    render() {
        //console.log('rendering NewListGroup')

        const items = this.renderItems()
        const background = this.props.darkMode === true ? 'rgb(36, 36, 36)' : 'rgb(255, 255, 255)'
        return (
            <>
                <div className="list" style={{background: background}}>
                    <img src={this.props.icon} alt="icon"></img>
                    <span onClick={this.setIsOpen} style={{fontSize: '1.5rem', marginLeft: '10px', display: 'inline-block', width: '80%', padding: '10px'}}>{this.props.artName}</span>
    
                    <Collapse isOpen={this.state.isOpen}>
                        <ListGroup style={{marginTop: '10px'}}>
    
                            {items}
    
                            <ListGroupItem  style={{textAlign: 'center', background: background}} onClick={this.setIsOpen}>
                                <i className="fas fa-chevron-up"></i>
                            </ListGroupItem>
                        </ListGroup>
                    </Collapse>
                </div>
    
            </>
        )
    }

}

