import { React, Component } from 'react'
import { Collapse } from 'reactstrap'
import broccoli from './broccoli.svg'
import apple from './apple.svg'
import spinach from './spinach.png'
import berry from './berry.svg'


import NewListGroup from './NewListGroup'


export default class List extends Component{

    state = {
        isOpen: false
    }

    setIsOpen = () => {
        this.setState({isOpen: !this.state.isOpen})
    }

    getData = () => {
        console.log('tasdfvs')
    }

    renderList = (art, artName, icon) => {
        const arr = this.props.list.data.filter(item => {
            return item.art === art
        })
        if (arr.length !== 0 ) {
            return (
                <NewListGroup 
                    getData={this.getData}
                    art = {art}
                    items = {arr}
                    artName = {artName}
                    icon = {icon}
                    published = {true}
                    darkMode = {this.props.darkMode}
                />
            )
        }
        else return <></>

    }

    render() {
        //console.log('rendering List')
        const background = this.props.darkMode === true ? 'rgb(36, 36, 36)' : 'rgb(255, 255, 255)'


        if (this.props.list.data.length === 0) {
            return (<></>)
        }

        const vegetables = this.renderList('vegetables', 'Овощи', broccoli)
        const fruits = this.renderList('fruits', 'Фрукты', apple)
        const greens = this.renderList('greens', 'Зелень', spinach)
        const berries = this.renderList('berries', 'Ягоды', berry)
        return (
            <>

                <div className="list" style={{padding: '0', background: background}}>
                    <span onClick={this.setIsOpen} style={{fontSize: '1.5rem', marginLeft: '10px', display: 'inline-block', width: '80%', padding: '10px 0'}}>{this.props.list.name === '' ? 'Новый список' : this.props.list.name}</span>
                    <Collapse isOpen={this.state.isOpen}>

                        {vegetables}
                        {fruits}
                        {greens}
                        {berries}

                    </Collapse>
                </div> 
            </>
        )
    }

}

